class DetailEquipe extends Component {
    constructor(props) {
        super(props)
        this.state = {
          user:{},
          ville:"",
          ok:"",
          logo:"",
          files_preview:[],
          files_name:[],
          multimedia:[],
        }
    }

    componentDidMount() {

      let  data=  JSON.parse(localStorage.getItem('token_auth'))
      var id=localStorage.getItem("idEquipe")
      let equipe=[]
      const requestOptions = {
        method: 'GET',
        headers: { 
          'Authorization':'Bearer '+data.token,
    
        },
      };
      let paye="",archive=""
  
      fetch(conf.url+"equipe/equipes/"+id,requestOptions)
      .then((response) => response.json())
      .then((data) => {
        console.log("data",data);
        
    
        $("#nomequipe").val(data.nomEquipe);
        $("#nomentreprise").val(data.nomEntreprise);
        $("#typeentreprise").val(data.typeEntreprise);
        $("#paye").val(data.paye?"OUI":"NON");
        $("#archive").val(data.archive?"OUI":"NON");

        try{

          if(data.multimedia?.file!==null && data.multimedia?.file!==""){
            storage.ref("images")
            .child(data.multimedia.file).getDownloadURL()
            .then((url) => {
            
                this.setState({logo:url})
            
            })
            }
        }catch(error){
  
        }
   
        })
    }
    handleRetour=()=>{
      this.setState({ok:<Redirect to='/detail-tournoi'/>})
    }

   
  render() {

    const { pick } = this.state
    const { selectedGroup } = this.state
    const { selectedLangue } = this.state
    const animatedComponents = makeAnimated();
    
  return (
        <>
          <React.Fragment>
            <div className="page-content">
             {this.state.ok}

            <Container fluid={false} className="justify-content-center">

              <Row className="justify-content-center">
              <Col md="12" lg="12" xl="12">

                {/* <Row ClassName="mb-3">
                  <Col md="8">
                      <div className="page-title-box d-flex align-items-center justify-content-between">
                        <h4 className="mb-0 font-size-18">Nouveau compte</h4>
                      </div>
                  </Col>
                </Row> */}
     
                

                <Card ClassName="mt-5">
                  {/* <CCardHeader className="p-4"> */}
                    <h1 className="p-4" >Détails de l'équipe</h1>
                  {/* </CCardHeader> */}

                  <CardBody>

                    <Row 
                      className="center" 
                      style={{justifyContent:"center"}}
                      // style={{width:"150px",height:"150px",borderRadius:"50px",display: 'flex',  justifyContent:'center', alignItems:'center'}}
                    >
                      <div 
                        // style={{width:"150px",height:"150px",borderRadius:"50px",justifyContent:"center"}}
                        style={{width:"250px",height:"250px",borderRadius:"50px",display: 'flex',  justifyContent:'center', alignItems:'center'}}
                      >
                        <CardImg  src={this.state.logo} alt="Photo de l'équipe" />
                      </div>
                    </Row>
                    <Row 
                      className="text-center mb-4" 
                      // style={{justifyContent:"center"}}
                      // style={{width:"150px",height:"150px",borderRadius:"50px",display: 'flex',  justifyContent:'center', alignItems:'center'}}
                    >
                      {
                        this.state.logo!==""?
                        <div >
                        <a href={this.state.logo} target="_blank"  style={{color:"#338C36"}} download> <i className="bx bxs-download bx-sm" style={{color:"#338C36"}}/> Télécharger la photo de l'équipe</a>
                      </div>:null
                      }
                    </Row>
                    <Row> 
                      <Col md="6">
                        <label>Nom de l'équipe</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="nomequipe"
                          readOnly
                        />
                      </Col>
                      <Col md="6">
                        <label>Nom de l'entreprise</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="nomentreprise"
                          readOnly
                        />
                      </Col>
                    </Row>

                    <Row className="mt-4">
                      <Col md="4">
                        <label>Type d'entreprise</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="typeentreprise"
                          readOnly
                        />
                      </Col>
                      <Col md="4">
                        <label>Payé / Impayé</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="paye"
                          readOnly
                        />
                      </Col>
                      <Col md="4">
                        <label>Archivé / Non archivé</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="archive"
                          readOnly
                        />
                      </Col>
                    </Row>
                  
                    {/* {
                      this.state.files_preview.map((element,idx)=>(
                        <Row className="mt-2">
                          <Col md="12" sm="12" lg="4" style={{textAlign:"center"}} >
                            <img style={{width:"900px",heigth:"300px",marginLeft:"30px"}} class="preview" src={element}/>
                          </Col>
                        </Row>
                      ))
                    } */}
                    <Row className="mt-4">
                      <Col md="3">
                        <Button onClick={this.handleRetour} color="danger" block className="mb-2">
                          Retour
                        </Button>
                      </Col>
                    </Row>

                  </CardBody>
                </Card>
              </Col>
            </Row>
            
          </Container>
        </div>
             
      </React.Fragment>
    </>
        )}

  }
  export default DetailEquipe